@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Unicase:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Unicase:wght@300&family=Noto+Sans+SC:wght@500&display=swap);

/* resume css */
.res {
    display: inline-flex;
    border: 3px solid lightblue;
    padding: 20px;
    margin: 20px;
}

 .react-pdf__Document {
    /* border: 3px solid black; */

}

.resume-container{
    display: flex;
    justify-content: center;
}

.connections ul{
    display: flex;
    /* border: solid 1px black; */
    justify-content: space-evenly;
    font-size: 25px;
}

.heading {
    display: flex;
    justify-content: center;
    font-family: 'Dancing Script', cursive;
    font-size: 3em;
    letter-spacing: 10px;
    font-weight: 700;
    
}

body{
  background: linear-gradient(-35deg, lightblue, pink);
}
/* projects css here */
.projects-container {
     display: flex;
    /* flex-direction: row;
    justify-content: space-around; */
}

.project-title{
    text-align: center;
    font-family: 'Judson', serif;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    font-weight: 20px;
}

.card {
    width: 25%;
    display: inline-flexbox;
    /* display: flex; */
    /* flex-wrap: wrap; */
    /* flex-direction: row; */
    margin: 1rem 2rem;
    justify-content: space-between;
    align-items: center;

    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    /* border: 5px solid #2b2f77; */
    cursor: pointer;
    text-align: center;
   
    border-radius: 15px;
    background: #eeeeee;
    padding: 2rem;

    font-family: 'Judson', serif; 
}

/* home component */

.social-handles{
    font-size: 25px;
    display: flex;
    justify-content: space-evenly;
    
}

.profile-pic{
    display: flex;
    justify-content: center;
    /* float: right; maybe use this */
}

img, .akiko-image{
    border-radius: 40px;
    width: 10em;
    margin-top: 10px;

}

.introduction{
background-color: whitesmoke;
border: 1px solid black;
/* display: flex; */
}

h1, .about-me{
    text-align: center;
}


.card-img-top {
    width: 100%;
    /* height: auto;
    width: auto; */
     
}

/* Navigation bar css here */

li {
    list-style-type: none;
    display: inline;
    padding: 8px;
    float: left;
}

li a {
    display: block;
    color: whitesmoke;
    text-align: center;
    padding: 10px 15px;
    text-decoration: none;
    border-radius: 10px;
}

.navbar {
    /* background-color: #2b2f77; */
    overflow: hidden;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}


li a:hover {
    background-color: #ddd;
    color: black;
}

li a.active {
    background-color: #4CAF50;
    color: white;
}

.navlist{
    font-size: 30px;
}

/* contact */

.contact{
    text-align: center;
}

/* blogs css */

.whole-card{
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    border: 5px solid rgba(39, 249, 198, 0.3);
    cursor: pointer;
    /* changes pointer for clickable objects */
    text-align: center;
    /* border-radius: 10px; */
    border-radius: 15px;
    background: #eeeeee;
    padding: 2rem;
    width: 25%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    display: inline-grid;
    margin: 1rem 2rem;
    color: #1a436b;
}

.blog__topImg{
    width: 100%;
}


/* quotes generator css here */

.quotes-container{
    display: flex;
    justify-content: center;
}

#quotes-box{
    display: inline-block;
    background-color: whitesmoke;
    border-radius: 10px 0 10px 0;
    /* margin: 30px 0 0 30px;
    padding: 25px; */
    margin: 10rem auto;
    width: 40rem;
    /* box-sizing: border-box; */
    padding: 3rem;
}

#author{
    font-family: 'Noto Sans SC', sans-serif;
    display: flex;
    justify-content: flex-end;
    font-size: 1.5rem;

}

img, .do-better-gif{
    border-radius: 10px;
}

#text{
    font-family: 'Noto Sans SC', sans-serif;
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

#new-quote{
    padding: 1em;
    border-radius: 10px;
    font-size: 0.9rem;
    font-weight: 800;
    background-color: lightpink;
    border: 1px solid lightblue;
}

#new-quote:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  }


